@use '../../00-base/init' as *;

.m-card {
  /**
    * @tokens Cards Component
    * @presenter Color
  */

  --ctek-card-padding-block: var(--ctek-global-space-205);
  --ctek-card-padding-inline: var(--ctek-global-space-205);
  --ctek-card-padding: var(--ctek-card-padding-block) var(--ctek-card-padding-inline);
  --ctek-card-border-width: 1px;
  --ctek-card-border-color: var(--ctek-global-color-gray-100);
  --ctek-card-border-style: solid;
  --ctek-card-border-radius: var(--ctek-global-radius-2);
  --ctek-card-width: 100%;
  --ctek-card-color-background: var(--ctek-global-color-white);

  /**
    * @tokens-end
  */
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: var(--ctek-card-width);
  padding: var(--ctek-card-padding);
  overflow: hidden;
  background-color: var(--ctek-card-color-background);
  border: var(--ctek-card-border-width) var(--ctek-card-border-style) var(--ctek-card-border-color);
  border-radius: var(--ctek-card-border-radius);
}

.m-card__media {
  margin-bottom: var(--ctek-global-space-205);
}

.m-card__heading {
  margin-top: 0;
  margin-bottom: var(--ctek-global-space-05);
  font-size: var(--ctek-global-fs-4);
  font-family: var(--ctek-global-ff-secondary);
  line-height: var(--ctek-global-lh-sm);
}

.m-card__body {
  font-family: var(--ctek-global-ff-primary);
}

.m-card__cta {
  margin-top: auto;
}
